import React from 'react';
import './style.scss';
import { Link } from 'react-router-dom';

import charator from './slice_a.svg';

class Scene extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      showAction: false,
      showQuestion: false,
    };
    
    const scene = props.script.scene[props.match.params.id];
    console.log(scene);
  }

  componentDidMount() {
    const scene = this.props.script.scene[this.props.match.params.id];

    if(scene.action) {
      this.timeoutAction = window.setTimeout(() => {
        this.setState({showAction: true});
      }, scene.startTime.getTime() + scene.action.appear - (new Date()).getTime());
    }

    if(scene.question) {
      this.timeoutQuestion = window.setTimeout(() => {
        this.setState({showQuestion: true});
      }, scene.startTime.getTime() + scene.question.appear - (new Date()).getTime());
    }

    this.props.playAudio(this.props.match.params.id, scene.stopLoop);
  }

  componentDidUpdate(prevProps) {
    if(prevProps.match.params.id !== this.props.match.params.id) {
      
      console.log('場景更換');

      const scene = this.props.script.scene[this.props.match.params.id];

      this.setState({
        showAction: false,
        showQuestion: false,
      });

      window.clearTimeout(this.timeoutAction);
      window.clearTimeout(this.timeoutQuestion);

      if(scene.action) {
        this.timeoutAction = window.setTimeout(() => {
          this.setState({showAction: true});
        }, scene.startTime.getTime() + scene.action.appear - (new Date()).getTime());
      }

      if(scene.question) {
        this.timeoutQuestion = window.setTimeout(() => {
          this.setState({showQuestion: true});
        }, scene.startTime.getTime() + scene.question.appear - (new Date()).getTime());
      }
  

      this.props.playAudio(this.props.match.params.id, scene.stopLoop);
    }
  }

  render() {
    const scene = this.props.script.scene[this.props.match.params.id];
    const backgroundImage = `url(${process.env.PUBLIC_URL + '/static/images/' + scene.screen[0].background})`;
    return (<div className="scene__container">
      <div className="background__container" style={{backgroundImage: backgroundImage}}>
        <div className="screen__container">
          <h2>{scene.title}</h2>
          <h3>{scene.subtitle}</h3>
          <div className="controll__container">
            <img className="charator" src={charator}></img>
            {scene.action && this.state.showAction && (<React.Fragment>
              <Link to={scene.action.nextScene}>{scene.action.buttonName}</Link>
              <img style={{opacity:0}} className="charator" src={charator}></img>
            </React.Fragment>)}
          </div>
          {scene.question && this.state.showQuestion && (<div className="question__container">
            <div className="question__dialog">{scene.question.title}</div>
            {scene.question.answer.map((answer) => <Link key={answer.nextScene} to={answer.nextScene}>{answer.buttonName}</Link>)}
          </div>)}
        </div>
      </div>
    </div>);
  }
}

export default Scene;