import React from 'react';
import './style.scss';
import { Link } from 'react-router-dom';

class Countdown extends React.Component {

  constructor(props) {
    super();
    let t = props.time - new Date();
    this.state = {
      d: Math.floor(t / 60 / 60 / 24 / 1000),
      h: Math.floor(t / 1000/ 60 / 60 % 24),
      m: Math.floor(t / 1000/ 60 % 60),
      s: Math.floor(t / 1000 % 60),
      start: t < 0
    };
  }

  componentDidMount() {
    this.interval = window.setInterval(() => {
      let t = this.props.time - new Date();
      this.setState({
        d: Math.floor(t / 60 / 60 / 24 / 1000),
        h: Math.floor(t / 1000/ 60 / 60 % 24),
        m: Math.floor(t / 1000/ 60 % 60),
        s: Math.floor(t / 1000 % 60),
        start: t < 0
      });
      if(t < 0) {
        window.clearInterval(this.interval);
      }
    }, 1000);
  }

  componentWillUnmount() {
    window.clearInterval(this.interval);
  }

  render() {
    const { d, h, m, s, start } = this.state;
    const { initialScene } = this.props;
    return (<div className="countdown__container">
      { !start && (<div className="countdown__layout">
        <h3>活動倒數</h3>
        <div className="countdown__timer">
          <span className="countdown__field">{d}</span>天
          <span className="countdown__field">{h}</span>時
          <span className="countdown__field">{m}</span>分
          <span className="countdown__field">{s}</span>秒
        </div>
      </div>) }
      { start && (<div className="countdown__layout">
        <Link to={"/scene/" + initialScene}>進入</Link>
        </div>)}
    </div>);
  }
}

export default Countdown;