import React from 'react';
import './App.scss';
import {
  Switch,
  Route,
  // Link
} from 'react-router-dom';

import Welcome from './components/Welcome';
import Scene from './components/Scene';
import script from './script';

class App extends React.Component {

  constructor() {
    super();
    
    let useragent = navigator.userAgent;
    let rules = ['WebView','(iPhone|iPod|iPad)(?!.*Safari/)','Android.*(wv|.0.0.0)'];
    let regex = new RegExp(`(${rules.join('|')})`, 'ig');

    this.state = {
      detect: {
        standalone: window.matchMedia('(display-mode: standalone)').matches || navigator.hasOwnProperty('standalone'),
        ios: (new RegExp('(iPhone|iPod|iPad)')).test(useragent),
        android: (new RegExp('Android')).test(useragent),
        chrome: (navigator.userAgent.indexOf("Chrome/") !== -1),
        safari: (navigator.userAgent.indexOf("Safari/") !== -1 && navigator.userAgent.indexOf("Chrome/") === -1),
        webview: regex.test(useragent)
      }
    };

    window.setAppStatus = (e) => {
      console.log(e);
    }
  }

  componentDidMount() {
    document.addEventListener('touchstart', () => {
      this.audio.play();
    });

    this.loopListener = () => {
      this.audio.src = process.env.PUBLIC_URL + "/static/media/BGM.m4a";
      this.audio.loop = true;
    };
    this.audio.addEventListener('ended', this.loopListener);
  }

  playAudio = (id, stopLoop) => {
    try{
      this.audio.src = process.env.PUBLIC_URL + `/static/media/${id}.m4a`;
      this.audio.loop = false;
      if(stopLoop) {
        console.log('listener' + this.loopListener);
        this.audio.removeEventListener('ended', this.loopListener);
        console.log('listener 已經移除');
      }
    } catch(exception) {
      window.setTimeout(() => {
        this.playAudio(id);
      }, 100);
    }
  }

  render() {
    const { detect } = this.state;
    return (
      <div className="App">
        <Switch>
          <Route exact path="/">
            <Welcome detect={detect} script={script} />
          </Route>
          <Route path="/scene/:id" render={routeProps => (<Scene {...routeProps} playAudio={this.playAudio} script={script}></Scene>)} />
        </Switch>
        <audio autoPlay={true} ref={(element) => { this.audio = element; }}>
          <source src={process.env.PUBLIC_URL + "/static/media/BGM.m4a"}></source>
        </audio>
      </div>
    );
  }
}

export default App;
